.app-wrapper {

    width: 90%;
    margin: 0 auto;
    background-color: #b1e7c0;
    display: grid;
    grid-template-rows: 100px 1fr;
    grid-template-columns: 150px 1fr;
    grid-gap: 10px;
    grid-template-areas:
    'h h'
    'n m';
}

.mainContent {
    grid-area: m;
    background-color: #383fcc;

}




